import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Row, Table } from 'reactstrap';
import { Rep } from '../Repetition/Rep';

export const Serie = ({ title, series }) => {
  const [_series, setSeries] = useState(series);
  const [_title, setTitle] = useState(title);

  useEffect(() => {}, []);

  const handleOnSave = () => {
    setIsEditable(false);
  };

  const handleOnAdd = () => {
    setSeries([...series, series.length + 1]);
  };

  const handleOnDelete = () => {
    setSeries(series.filter(id => id !== series.length));
  };

  return (
    <Container>
      <Row>
        <h2>{title}</h2>
      </Row>
      <Row>
        <Table>
          <thead>
            <tr>
              <th>#</th>
              <th>#repetitions</th>
              <th>weight</th>
            </tr>
          </thead>
          <tbody>
            {series.map(item => (
              <Rep key={r} serie={item}></Rep>
            ))}
          </tbody>
        </Table>
      </Row>
      <Row>
        <Col xs={8}>
          <Button color="success" onClick={handleOnSave}>
            Save
          </Button>
        </Col>
        <Col xs={4}>
          <Button color="danger" onClick={handleOnDelete}>
            -
          </Button>
          <Button color="primary" onClick={handleOnAdd}>
            +
          </Button>
        </Col>
      </Row>
    </Container>
  );
};
